.itemimg {
  width: 300px;
  height: 300px;
  -webkit-animation-name: zoomin-d;
          animation-name: zoomin-d;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes zoomin-d {
  from {
    height: 200px;
    width: 200px;
  }

  to {
    height: 300px;
    width: 300px;
  }
}

@keyframes zoomin-d {
  from {
    height: 200px;
    width: 200px;
  }

  to {
    height: 300px;
    width: 300px;
  }
}

